html {
    font-size: 62.5%;
}

body {
    font-family: 'Open Sans', sans-serif;
}

.wrapper {
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
}

h1 {
    color: rgba(255,255,255,0.8);
    font-weight: 900;
    text-shadow: 5px 0px 10px rgba(0,0,0,.5);   
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-80%, 30%);
    text-align: center;
}